/* TODO: variables from .item */
.item-date {
  --time-day-background-color: transparent;
  --time-night-background-color: rgba(40, 0, 60, 0.05);
  --item-date-day-color: rgba(96, 125, 139, 0);
  --item-date-day-hover-color: rgba(96, 125, 139, 0.45);
  --item-date-weekday-color: rgba(96, 125, 139, 0.45);
  --items-per-screen: 12;
}

.item-date {
  font-size: 3vh;
  height: calc(100% / var(--items-per-screen));
}

.item-date__content {
  height: 100%;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.item-date__date {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  padding-left: calc(33.33% + 5px);
}

.item-date:hover .item-date__day {
  color: var(--item-date-day-hover-color);
}

.item-date_time_day {
  background-color: var(--time-day-background-color);
}

.item-date_time_night {
  background-color: var(--time-night-background-color);
}

.item-date__day {
  --width: 1.25em;
  --margin-left: 0.325em;
  text-align: left;
  color: var(--item-date-day-color);
  -webkit-transition: color ease 0.35s;
  -o-transition: color ease 0.35s;
  transition: color ease 0.35s;
  width: var(--width);
  margin-right: calc(-1 * (var(--width) + var(--margin-left)));
  margin-left: var(--margin-left);
}

.item-date__weekday {
  color: var(--item-date-weekday-color);
}



